<template>
  <footer :class="[ 'footer', { 'footer--hidden': hidden } ]" @keyup.enter="submit()">
    <div :class="[ 'drawer', { 'drawer--active': searchVisible && !hidden } ]">
      <div class="search">
        <button class="search__button" @click="submit()">
          <icon-search class="search__icon" />
        </button>

        <input
          class="search__input"
          type="text"
          placeholder="Введите название работы"
          v-model.trim="query"
        />
        <span class="search__clear" v-show="query.length" @click="setQuery('')">
          <icon-close class="search__icon" />
        </span>
      </div>

      <section class="history" v-show="getHistory.length">
        <div class="history__title">
          <span>Недавние поисковые запросы:</span>
          <span class="history__clean" @click="cleanHistory">Очистить</span>
        </div>

        <div class="history__item" v-for="(item, i) in getHistory.slice().reverse().slice(0, 7)" :key="i">
          <div class="history__label" @click="submit(item)">{{ item }}</div>
          <span class="history__remove" @click="delHistory(item)"><icon-close /></span>
        </div>
      </section>

      <c-tags class="tags" :items="getTags" />
    </div>

    <nav class="menu">
      <router-link
        :class="[ 'menu__item', { 'menu__item--active': getActive === i && !searchVisible } ]"
        :to="item.route"
        v-for="(item, i) in menu"
        :key="i"
        @click.native="searchVisible = false"
      >
        <component class="menu__icon" :is="item.icon" />
        <span class="menu__label">{{ item.label }}</span>
      </router-link>

      <span :class="[ 'menu__item', { 'menu__item--active': searchVisible } ]" @click="searchVisible = true">
        <icon-search class="menu__icon" />
        <span class="menu__label">Поиск</span>
      </span>
    </nav>

    <c-blackout v-model="searchVisible" />
  </footer>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'l-footer-mobile',

  components: {
    cBlackout: () => import('@/components/Blackout'),
    cTags: () => import('@/components/Tags'),
    iconSearch: () => import('mdi-vue/Magnify'),
    iconClose: () => import('mdi-vue/Close')
  },

  props: {
    menu: {
      type: Array,
      required: true
    },
    hidden: {
      type: Boolean,
      default: false
    }
  },

  data: () => ({
    searchVisible: false,
    query: ''
  }),

  created () {
    this.query = this.getQuery
  },

  computed: {
    ...mapGetters({
      getQuery: 'search/getQuery',
      getHistory: 'search/getHistory',
      getTags: 'worklist/getTags'
    }),

    getActive () {
      return this.menu.findIndex(item => item.route.name === this.$route.name)
    }
  },

  methods: {
    delHistory (item) {
      this.$store.commit('search/delHistory', item)
    },
    cleanHistory () {
      this.$store.commit('search/cleanHistory')
    },
    setQuery (str) {
      this.query = str
      this.$store.commit('search/setQuery', str)
    },
    submit (query) {
      this.searchVisible = false

      if (query) {
        this.query = query
        this.$store.dispatch('search/submit', query)
      } else {
        this.$store.dispatch('search/submit', this.query)
      }

      this.$router.push({ name: 'home' }).catch(() => {})
    }
  }
}
</script>

<style scoped>
  .footer {
    position: sticky;
    width: 100%;
    bottom: 0;
    z-index: 10;
    transform-origin: 0 100%;
    transition: transform .3s .1s;
  }
  .footer--hidden {
    position: fixed;
    transform: translateY(100%);
  }

  .menu {
    display: flex;
    align-items: center;
    position: relative;
    z-index: 10;
    background: #fff;
    border-top: 1px solid #eee;
  }
  .menu__item {
    position: relative;
    color: hsla(var(--ui-primary), .5);
    text-decoration: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    height: 35px;
    padding: 5px;
    flex: 1;
    transition: color .2s;
  }
  .menu__item--active {
    color: hsl(var(--ui-active));
  }
  .menu__icon {
    width: 22px;
    display: block;
  }
  .menu__item--active .menu__icon {
    animation: menu .4s ease;
  }
  .menu__label {
    font-size: 10px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .drawer {
    position: absolute;
    bottom: 35px;
    box-sizing: border-box;
    width: 100%;
    z-index: 10;
    background: #fff;
    padding: 10px;
    min-height: 100px;
    transform-origin: 100% 100%;
    transform: scaleY(0);
    transition: transform .2s;
  }
  .drawer--active {
    transform: scaleY(1);
    transition: transform .2s .3s;
  }

  .search {
    position: relative;
    background: #fff;
    height: 40px;
    width: inherit;
    border-radius: 3px;
    display: flex;
    align-items: center;
    z-index: 10;
    border: 1px solid #eee;
  }
  .search__input {
    flex: 1;
    height: 100%;
    width: 100%;
    outline: none;
    border: none;
    padding: 10px;
    font-family: inherit;
    font-size: 18px;
    color: hsl(var(--ui-primary));
    box-sizing: border-box;
  }
  .search__clear {
    width: 18px;
    margin: 0 10px 0 5px;
    cursor: pointer;
    color: hsl(var(--ui-primary));
    transition: color .2s;
  }
  .search__clear:hover {
    color: hsl(var(--ui-active));
  }
  .search__button {
    user-select: none;
    background: none;
    border: none;
    outline: none;
    cursor: pointer;
    width: 22px;
    padding: 0;
    margin: 0 0 0 10px;
    color: hsl(var(--ui-primary));
    transition: color .2s;
  }
  .search__button:hover {
    color: hsl(var(--ui-active));
  }

  .history {
    display: flex;
    flex-wrap: wrap;
    margin-top: 15px;
  }
  .history__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 14px;
    font-weight: 500;
    width: 100%;
    margin-bottom: 10px;
    color: hsl(var(--ui-primary));
  }
  .history__clean {
    font-size: 12px;
    font-weight: 400;
    cursor: pointer;
  }
  .history__item {
    margin: 0 10px 10px 0;
    background: hsla(var(--ui-primary), .1);
    border-radius: 3px;
    color: hsl(var(--ui-primary));
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: 400;
    cursor: pointer;
    user-select: none;
    transition: color .2s, background .2s;
  }
  .history__item:hover {
    background: hsla(var(--ui-active), .1);
    color: hsl(var(--ui-active));
  }
  .history__label {
    padding: 5px 0 5px 10px;
    max-width: 150px;
    align-items: center;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .history__remove {
    display: block;
    width: 14px;
    padding: 5px;
  }

  .tags {
    padding: 15px 0;
    font-size: 13px;
    justify-content: center;
  }

  @keyframes menu {
    50% {
      transform: scaleY(.8);
    }
    100% {
      transform: scaleY(1);
    }
  }
</style>